.post {
        width: 100%;
        margin-top: 15px;
        border-radius: 15px;
        background-color: #fff;
        box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
        height: 100%;
}

.post__top {
        display: flex;
        position: relative;
        align-items: center;
        padding: 15px;
}

.post__avatar {
        margin-right: 10px;
}

.post__topInfo > h3 {
        font-size: medium;
}

.post__bottom {
        margin-top: 10px;
        margin-bottom: 10px;
        padding: 15px 25px;
}
.post__image > img {
        width: 100%;
}

.post__actionView{
        padding-top: 10px;
        margin-left: 4rem;
        margin-right: 4rem;
        display: flex;
        justify-content: space-between;
        font-size: medium;
        color: grey;
        cursor: pointer;
        padding: 10px;

}

/* .post__leftSide{
        display: flex;
} */


/* .post__rightSide{
        display: flex;
        justify-content: space-between;
} */

.post__likeView{
        display: flex;
        margin-left: 0.5rem;     
}

.post__commentView, .post__shareView{
        display: flex;
}

.post__likeView>p, .post__commentView>p, .post__shareView > p{
        margin-left: 0.5rem;
}


.post__options {
        padding-top: 10px;
        border-top: 1px solid lightgray;
        display: flex;
        justify-content: space-evenly;
        font-size: medium;
        color: gray;
        cursor: pointer;
        padding: 15px;
}

.post__option {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 5px;
        flex: 1;
}

.post__option > p {
        margin-left: 10px;
}

.post__option:hover {
        background-color: #eff2f5;
        border-radius: 10px;
}


